import React, { Component } from "react";
import myPic from "../Images/56325290_321212165100586_5703099949223247872_n.png";
import redux from "../Images/redux.png";
import jQuery from "../Images/jquery.png";

export default class About extends Component {
  render() {
    return (
      <main className="container mt-4">
        <h1 className="text-center" id="about">
          About me
        </h1>
        <div className="text-center mt-4">
          <img src={myPic} alt="Me" className=" rounded-circle" />
        </div>
        <section className="mt-4 text-center">
          <p>
            My name is Michał Pyszyński. I am looking for a job as a junior
            front-end developer
          </p>
          <p>
            I am currently focusing on creating websites based on{" "}
            <strong>React.js</strong>, <strong>Redux</strong>,{" "}
            <strong>Bootstrap</strong>.
          </p>
          <p>
            I would like to find a job where i would be able to improve my
            skills to be better developer and have fun while doing it.
          </p>
          <a
            href="https://github.com/Vigome"
            target="_blank"
            rel="noopener noreferrer"
            className="btn btn-primary profiles-button"
          >
            Github
          </a>
          <a
            href="https://drive.google.com/open?id=1yU0usBA1iM3HmsYGv9ZljVZY4QpaxmK_"
            target="_blank"
            rel="noopener noreferrer"
            className="btn btn-primary profiles-button ml-3 mx-ml-5"
          >
            Cv
          </a>

          {/* <a
            href="https://www.codewars.com/users/Vigome"
            target="_blank"
            rel="noopener noreferrer"
            className="btn btn-primary profiles-button"
          >
            Codewars{" "}
          </a> */}

          <h1 className="mt-5">Skills</h1>
          <div className="row mt-4">
            <div className="col-12 col-md-6 mt-3 m-md-0">
              <h5>Front-end</h5>
              <div className="row">
                <div className="col-12 row my-3">
                  <i className="fab fa-html5 about-icon col-2 offset-4" />
                  <h3 className="m-0">HTML5</h3>
                </div>
                <div className="col-12 row my-3">
                  <i className="fab fa-js-square about-icon col-2 offset-4" />
                  <h3 className="m-0">JavaScript</h3>
                </div>
                <div className="col-12 row my-3">
                  <i className="fab fa-css3 about-icon col-2 offset-4" />
                  <h3 className="m-0">CSS</h3>
                </div>
                <div className="col-12 row my-3">
                  <i className="fab fa-sass about-icon col-2 offset-4" />
                  <h3 className="m-0">SCSS</h3>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 mt-5 m-md-0">
              <h5>Frameworks</h5>
              <div className="row">
                <div className="col-12 row my-3">
                  <i className="fab fa-react about-icon col-2 offset-4" />
                  <h3 className="m-0">React.js</h3>
                </div>
                <div className="col-12 row my-3">
                  <div className="col-2 offset-4">
                    <img src={redux} alt="redux" className=" about-icon" />
                  </div>
                  <h3 className="m-0">Redux</h3>
                </div>
                <div className="col-12 row my-3">
                  <i className="fab fa-bootstrap about-icon col-2 offset-4" />
                  <h3 className="m-0">Bootstrap</h3>
                </div>
                <div className="col-12 row my-3">
                  <div className="col-2 offset-4">
                    <img src={jQuery} alt="jquery" className=" about-icon" />
                  </div>
                  <h3 className="m-0">jQuery</h3>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
    );
  }
}
