import React, { Component } from "react";
import * as emailjs from 'emailjs-com'


export default class Contact extends Component {
  constructor(){
    super()
    this.state = {
      name: '',
      email: '',
      message: '',
    }
    this.handleSubmit = this.handleSubmit.bind(this)
    }
      handleChange = (event) => {
        const {name, value} = event.target
        this.setState({
          [name]: value
        })
      }
      async handleSubmit(event) {
        event.preventDefault()
        console.log("do i work/")
        let templateParams = {
          name: this.state.name,
          email: this.state.email,
          message: this.state.message
        }
        emailjs.send("gmail", "portfolioemail", templateParams, 'user_tfXHYy5PbkgLKA0MfxSnI').then(function(){
          alert("Thanks for you message!")
        })
        this.setState({
          name: '',
          email: '',
          message: ''
        })
      }
  render() {
    return (
      <section className="contact">
        <div className="container pt-5">
          <h1 className="text-center" id='contact'>Contact</h1>
          <form className="" >
            <div className='form-group row'>

            <div class="col-12 col-md-6 offset-md-3">
              <label class="sr-only" for="nameInput">
                Name
              </label>
              <div class="input-group mb-2">
                <div class="input-group-prepend">
                  <div class="input-group-text"><i className="fas fa-user"></i></div>
                </div>
                <input
                  type="text"
                  class="form-control"
                  id="nameInput"
                  placeholder="Name"
                  required
                  name='name'
                  value={this.state.name}
                  onChange={(event) => this.handleChange(event)}
                />
              </div>
            </div>
            <div class="col-12 col-md-6 offset-md-3">
              <label class="sr-only" for="emailInput">
                E-mail
              </label>
              <div class="input-group mb-2">
                <div class="input-group-prepend">
                  <div class="input-group-text"><i className="fas fa-envelope"></i></div>
                </div>
                <input
                  type="email"
                  class="form-control"
                  id="emailInput"
                  placeholder="Name@example.com"
                  required
                  name='email'
                  value={this.state.email}
                  onChange={(event) => this.handleChange(event)}
                />
              </div>
            </div>
            <div class="col-12 col-md-6 offset-md-3">
              <label class="sr-only" for="messageInput">
                Message
              </label>
              <div class="input-group mb-2">
                <div class="input-group-prepend">
                  <div class="input-group-text"><i className="fas fa-comment"></i></div>
                </div>
                <textarea
                  class="form-control"
                  id="messageInput"
                  rows="3"
                  required
                  placeholder='Message'
                  name='message'
                  value={this.state.message}
                  onChange={(event) => this.handleChange(event)}
                />
              </div>
            </div>
            </div>
            <div className='d-flex justify-content-center pb-2'>
            <div className='w-50 text-right'>
            <button type="submit" class="btn btn-success mr-1" onClick={(e) => this.handleSubmit(e)}>Send message</button>

            </div>

            </div>
          </form>
        </div>
      </section>
    );
  }
}
