import React, { Component } from 'react'
import classNames from 'classnames'

export default class Navbar extends Component {
  constructor(){
    super()
    this.state = {
      menuActive: false
    }
  }
  handleClick = () => {
    this.setState(prevState => ({
      menuActive: !prevState.menuActive
    }))
  }
  render(props) {
    return (
      <nav className={classNames('burger-nav rounded bg-primary', this.props.visible ? 'burger-show' : '')} onClick={() => this.handleClick()}>
      <ul className={classNames("d-flex burger-nav-active rounded-left align-items-center bg-primary flex-column flex-md-row justify-content-around", this.state.menuActive ? 'show' : '')}>
            <li className="nav-item">
                <a className="nav-link burger-link" disabled href="#start">
                Start
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link burger-link" href="#about">
                  About
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link burger-link" href="#projects">
                  Projects
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link burger-link" href="#contact">
                  Contact
                </a>
              </li>
            </ul>
      <div className={classNames('icon', this.state.menuActive ? 'active' : '')}></div>
    </nav>
    )
  }
}
