import React, { Component } from "react";
import "./App.css";
import Header from "./Components/Header";
import Navbar from "./Components/Navbar";
import About from "./Components/About";
import Projekty from "./Components/Projekty";
import Contact from "./Components/Contact";
import { Waypoint } from "react-waypoint";

export default class App extends Component {
  constructor(){
    super()
    this.state = {
      burgerVisible: false
    }
  }
  handleWaypointLeave = () => {
    this.setState({
      burgerVisible: true
    })
  }
  handleWaypointEnter = () => {
    this.setState({
      burgerVisible: false
    })
  }
  handleWaypointEnter2 = () => {
    this.setState({
      burgerVisible: true
    })
  }
  render() {
    return (
      <React.Fragment className="container-fluid">
        <Header />
        <Waypoint
          onEnter={() => this.handleWaypointEnter()}
          onLeave={() => this.handleWaypointLeave()}
        />
        {console.log(this.state.burgerVisible)}
        <Navbar visible={this.state.burgerVisible} />
        <About />
        <Waypoint
          onEnter={() => this.handleWaypointEnter2()}
        />
        <Projekty />
        <Waypoint
          onEnter={() => this.handleWaypointEnter2()}
        />
        <Contact />
      </React.Fragment>
    );
  }
}
